import React, { Component } from 'react'
import { Helmet } from 'react-helmet'
import { compose } from 'redux'
import Order from 'Shared/components/Order'
import withTranslations from 'Shared/hoc/withTranslations'
import {
  getPriceWithCurrency,
  getSelectedJobPosts,
  saveSelectedJobPosts,
  calculateJobsTotalPrice,
  calculateJobsTotalSavings,
  getLocationCode
} from 'Shared/helpers/payment'
import { NumericInput } from 'Shared/components/FormElements'
import { openIntercomWithNewMessage } from 'Intercom/components/IntercomComponent'
import NotificationService from 'Shared/services/NotificationService'
import './JobPostOrderReview.scss'
import { selectJobPostLimits } from 'Shared/constants/Job'
import { formatAfterFive, parseNumericInput } from 'Shared/helpers/formats'
import { pricingPage } from 'Shared/constants/MetaTags'

import EmployersService from 'Shared/services/EmployersService'

const employersService = new EmployersService()

class JobPostOrderReview extends Component {
  componentDidMount () {
    this.props.fetchCurrentLocationIfNeeded()
    this.props.isAuthenticated && employersService.checkoutStartedEvent({
      quantity: getSelectedJobPosts(),
      source: 'self-checkout',
      product: 'jobPost',
      locationCode: getLocationCode()
    })
    this.setState({data:  this.props.productDetails})
  }

  state = {
    jobPostsAmount: getSelectedJobPosts(),
    data: []
  }

  inputProps = () => ({
    input: {
      onChange: value => {
        this.setState({ jobPostsAmount: value })
        saveSelectedJobPosts(value)
      },
      value: this.state.jobPostsAmount
    },
    format: formatAfterFive(this.state.jobPostsAmount),
    parse: parseNumericInput(5),
    precision: 0,
    step: 1,
    meta: {}
  })

  get productAmount () {
    const { t } = this.props
    return (
      <div>
        {
          getSelectedJobPosts() < 6
            ? `${getSelectedJobPosts()} x ${t('plansPage.products.jobPost.title')}`
            : `${getSelectedJobPosts() - 1}+ ${t('plansPage.products.jobPost.titlePlural')}`
        }
        <div className='job-post-order__summary-description-wrapper'>
          <span className='job-post-order__summary-description'>
            {t('plansPage.products.jobPost.summaryDescription')}
          </span>
        </div>
      </div>
    )
  }

  get order () {
    const { t, price, currency } = this.props
    const { jobPostsAmount } = this.state
    const jobPost = this.state.data.job_post
    const jobPostPrice = jobPost ? jobPost[0].price : ''
    const postCurrency = jobPost ? jobPost[0].currency.toUpperCase() : ''
    const isBetweenOneAndFive = jobPostsAmount >= 1 && jobPostsAmount <= 5
    const savings = calculateJobsTotalSavings(jobPostsAmount, jobPostPrice)

    return (
      <div className='job-post-order'>
        <div className='job-post-order__order-product'>
          <div className='job-post-order__order-product-name'>
            {t('plansPage.products.jobPost.title')}
          </div>
          <div className='job-post-order__order-product-price'>
            {isBetweenOneAndFive && getPriceWithCurrency(jobPostPrice, postCurrency)}
            <span className='job-post-order__order-product-price-label'> {isBetweenOneAndFive ? t('order.perJobPost') : t('plansPage.products.jobPost.customPricing')} </span>
          </div>
        </div>
        <div className='job-post-order__select'>
          <div className='job-post-order__select-label'>
            {
              jobPostsAmount > 1 && jobPostsAmount <= 5
                ? <div className='job-post-order__card-select-jobs-label--green'>
                  <span>{t('plansPage.products.jobPost.savings')}</span>
                  &nbsp;{getPriceWithCurrency(savings, postCurrency)}&nbsp;
                </div>
                : t('plansPage.products.jobPost.selectLabel')
            }
          </div>
          <NumericInput {...this.inputProps()} min={selectJobPostLimits.min} max={selectJobPostLimits.max} />
        </div>
      </div>
    )
  }

  onCheckout = async () => {
    if (this.props.isAuthenticated) {
      await this.props.createProductTransaction(this.state.jobPostsAmount)
      const { t, paymentResponseStatus, orderRef } = this.props
      if (paymentResponseStatus !== 200) {
        return NotificationService.error(t('errors.transactionCreateError'))
      }
      return this.props.history.push(`/pricing/job-posts/checkout/${orderRef}`)
    } else {
      return this.props.history.push('/employer/sign-up?selfCheckout=jobPosts')
    }
  }

  render () {
    const { t, price, currency, orderRef } = this.props
    const { jobPostsAmount } = this.state
    const isBetweenOneAndFive = jobPostsAmount >= 1 && jobPostsAmount <= 5
    const jobPost = this.state.data.job_post
    const jobPostPrice = jobPost ? jobPost[0].price : ''
    const postCurrency = jobPost ? jobPost[0].currency.toUpperCase() : ''
    return (
      <div>
        <Helmet>
          <title>{pricingPage.title}</title>
          <meta name='description' content={pricingPage.description} />
          <meta property='og:title' content={pricingPage.title} />
          <meta property='og:type' content='website' />
          <meta property='og:image' content={pricingPage.image} />
          <meta property='og:image:width' content='1200' />
          <meta property='og:image:height' content='630' />
          <meta property='og:url' content={pricingPage.url} />
          <meta property='og:description' content={pricingPage.description} />
        </Helmet>
        <Order
          reviewUrl={'/pricing/job-posts/review'}
          checkoutUrl={`/pricing/job-posts/checkout/${orderRef}`}
          disableCheckoutLink={orderRef === ''}
          renderVatInclusive={isBetweenOneAndFive}
          renderTotalPrice={isBetweenOneAndFive}
          order={this.order}
          productAmount={this.productAmount}
          price={getSelectedJobPosts() > 5 ? t('plansPage.products.jobPost.customPricing') : getPriceWithCurrency(calculateJobsTotalPrice(getSelectedJobPosts(), jobPostPrice), postCurrency)}
          benefits={[
            t('plansPage.products.jobPost.benefits.expiryPeriod'),
            t('plansPage.products.jobPost.benefits.companyPage'),
            t('plansPage.products.jobPost.benefits.applicantFilters'),
            t('plansPage.products.jobPost.benefits.support')
          ]}
          benefitsTitle={t('order.jobPostBenefitsTitle')}
          onCheckout={jobPostsAmount > 5 ? openIntercomWithNewMessage : this.onCheckout}
          checkoutButtonText={jobPostsAmount > 5 ? t('common.talkToSales') : t('order.checkout')}
        />
      </div>
    )
  }
}

export default compose(withTranslations)(JobPostOrderReview)



