import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import JobPostOrderReview from 'Shared/views/JobPostOrderReview'
import { fetchCurrentLocationIfNeeded } from 'Shared/ducks/geolocation'
import { getJobPostBasePrice, getPaymentCurrency } from 'Shared/selectors/geolocation'
import { createProductTransaction } from 'Shared/ducks/payment'
import { productTypes } from 'Shared/constants/Payment'
import { getPaymentOrderRef, getPaymentResponseStatus } from 'Shared/selectors/payment'
import { organisationAdminEmployerFullName } from 'Shared/selectors/employers'
import { isAdminEmployer, userEmail } from 'Shared/selectors/user'
import { getOrganisationEmployers } from 'Shared/ducks/employers'
import { getOrganisationName } from 'Shared/selectors/organisations'
import { getLocalData } from 'Shared/helpers/session'


const productDetails = getLocalData('product_details');

const mapStateToProps = state => {
  return ({
    userRole: state.user.role,
    price: getJobPostBasePrice(state),
    currency: getPaymentCurrency(state),
    isAuthenticated: state.user.isAuthenticated,
    orderRef: getPaymentOrderRef(state),
    paymentResponseStatus: getPaymentResponseStatus(state),
    isAdminEmployer: isAdminEmployer(state),
    adminFullName: organisationAdminEmployerFullName(state, state.user.details.organisationId),
    organisationName: getOrganisationName(state, state.user.details.organisationId),
    userEmail: userEmail(state),
    productDetails: productDetails
  })
}

const mapDispatchToProps = dispatch => ({
  fetchCurrentLocationIfNeeded: () => dispatch(fetchCurrentLocationIfNeeded()),
  createProductTransaction: quantity => dispatch(createProductTransaction(productTypes.jobPost, quantity)),
  getOrganisationEmployers: () => dispatch(getOrganisationEmployers())
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(JobPostOrderReview))



